"use client"

import { useEffect, useState } from "react"
import { useTheme } from "next-themes"
import { Sun, Moon } from "lucide-react"
import { ToggleGroup, ToggleGroupItem } from "@/components/ui/toggle-group"

export default function ThemeToggle() {
  const { theme, setTheme } = useTheme()
  const [mounted, setMounted] = useState(false)
  useEffect(() => setMounted(true), [])
  if (!mounted) return null

  const value = theme === "system" ? "system" : theme || "system"
  return (
    <ToggleGroup
      type="single"
      value={value as any}
      onValueChange={(v) => {
        if (!v) return
        setTheme(v)
      }}
      variant="outline"
      size="sm"
      className="bg-white dark:bg-transparent"
    >
      <ToggleGroupItem value="light" aria-label="Light" className="gap-1">
        <Sun className="size-4" />
        Light
      </ToggleGroupItem>
      <ToggleGroupItem value="dark" aria-label="Dark" className="gap-1">
        <Moon className="size-4" />
        Dark
      </ToggleGroupItem>
      <ToggleGroupItem value="system" aria-label="System" className="gap-1">
        System
      </ToggleGroupItem>
    </ToggleGroup>
  )
}
